const Consumer = RootContext.Consumer;
export const GlobalConsumer = (ChildComponent) => {
  return(
    class ParentConsumer extends Component {
      render(){
        return(
          <Consumer>
            {
              value => {
                return(
                  <ChildComponent {...this.props} {...value} />
                )
              }
            }
          </Consumer>
        );
      }
    }
  );
}

export default GlobalProvider