// Global Service
const Get = (path,) => {
  const promise = new Promise((resolve, reject) => {
    fetch(path,{
      method : "GET",
      headers : {
        'Accept': 'application/json',
        "Content-Type" : "application/json"
      }
    })
    .then((response) => {
      if(response.ok){
        resolve(response.json())
      } else {
        reject(new Error('error'))
      }
    }, (error) =>{
      reject(new Error(error.message));
    })
  })
  return promise;
}

