const getBlog = (data) => {
  let blogUrl = "https://www.codepelajar.com";
  let path = `${bloggerPathByUrl}?key=${key}&url=${blogUrl}`;
  return Get(path)
}

const getBlogPost = () => {
  // let blogUrl = "";
  let path = `${bloggerPathById}/${blogId}/posts?key=${key}&maxResults=7`;
  return Get(path);
}

